import React from 'react';
import classes from './ProfileInfo.module.css';
import styles from './ProfileInfo.module.css';
import Preloader from '../../common/Preloader/Preloader';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';

const ProfileInfo = (props) => {
    if (!props.profile) {
        return (
            <Preloader/>
        );
    }
    debugger
    return (
        <div>
            <div className={classes.description_block}>
                <img className={styles.photo}
                     src={props.profile.photos.small != null
                         ? props.profile.photos.small
                         : 'data:image/png;base64,'}/>
                <span
                    className={styles.name}>{props.profile.fullName === 'Vladislav_Smolin' ? 'Maria Kolos' : props.profile.fullName}</span>
                <div>{props.profile.lookingForAJobDescription}</div>
                {props.id === props.userIdInURL
                    ? <ProfileStatusWithHooks
                        status={props.status}
                        updateStatus={props.updateStatus}/>
                    : <div title='Only read' className={styles.status + ' ' + styles.statusText}>
                        Status : {props.status ? props.status : 'No status'
                    }
                    </div>
                }
            </div>
        </div>
    );
};

export default ProfileInfo;
